import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { IconButton, TextField } from '@mui/material';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import FilterBrand from './FilterBrand';
import { useSelector, useDispatch } from 'react-redux';
import { setPrice } from '../../redux/slices/filterPrice';
import { setBrand } from '../../redux/slices/filterBrand';
import { setSearch } from '../../redux/slices/filterSearch';
import { useCallback } from 'react';
import { debounce } from 'lodash';
import { setInputPrice } from '../../redux/slices/forInputPrice';
import { setInputSearch } from '../../redux/slices/forInputSearch';

const StyledMenu = styled((props) => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right',
    }}
    {...props}
  />
))(({ theme }) => ({
  '& .MuiPaper-root': {
    borderRadius: 6,
    marginTop: theme.spacing(1),
    minWidth: 180,
    color: theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
    boxShadow:
      'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
    '& .MuiMenu-list': {
      padding: '4px 0',
    },
    '& .MuiMenuItem-root': {
      '& .MuiSvgIcon-root': {
        fontSize: 18,
        color: theme.palette.text.secondary,
        marginRight: theme.spacing(1.5),
      },
      '&:active': {
        backgroundColor: alpha(theme.palette.primary.main, theme.palette.action.selectedOpacity),
      },
    },
  },
}));

export default function Filter() {
  const inputPrice = useSelector((state) => state.forInputPrice.inputPrice);
  const dispatch = useDispatch();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const OptimizationPrice = useCallback(
    debounce((value) => {
      dispatch(setPrice(Number(value)));
    }, 1000),
    [],
  );
  const onChangeInput = (event) => {
    const {
      target: { value },
    } = event;
    OptimizationPrice(value);
    dispatch(setInputPrice(value));
    dispatch(setSearch(''));
    dispatch(setBrand(''));
    dispatch(setInputSearch(''));
  };

  return (
    <div>
      <IconButton
        id="demo-customized-button"
        aria-controls={open ? 'demo-customized-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        variant="contained"
        disableElevation
        onClick={handleClick}
        endIcon={<KeyboardArrowDownIcon />}
      >
        <FilterAltIcon />
      </IconButton>
      <StyledMenu
        id="demo-customized-menu"
        MenuListProps={{
          'aria-labelledby': 'demo-customized-button',
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
      >
        <MenuItem disableRipple>
          <TextField
            InputLabelProps={{ shrink: true }}
            onChange={onChangeInput}
            type="number"
            value={inputPrice}
            id="outlined-basic"
            label="Цена, руб"
            variant="outlined"
            sx={{ ml: 1, mt: 1, width: '33.7ch' }}
          />
        </MenuItem>
        <MenuItem disableRipple>
          <FilterBrand {...{ ...handleClose }} />
        </MenuItem>
      </StyledMenu>
    </div>
  );
}
